.widget {
  display: flex;
  justify-content: space-between;
  flex: 1;
  padding: 10px;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.37);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 10px;
  height: 150px;

  .left,
  .right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .title {
      font-weight: bold;
      font-size: 14px;
      color: rgb(177, 176, 176);
    }

    .counter {
      font-size: 28px;
      font-weight: 400;
    }

    .link {
      width: max-content;
      font-size: 12px;
      border-bottom: 1px solid gray;
    }

    .percentage {
      display: flex;
      align-content: center;
      font-size: 14px;

      &.positive {
        color: rgba(0, 255, 0, 0.528);
      }
      &.negative {
        color: rgba(255, 0, 0, 0.528);
      }
    }

    .icon {
      font-size: 30px;
      padding: 5px;
      border-radius: 5px;
      align-self: flex-end;
    }
  }
}
